*{
  margin: 0px;
  list-style: none;
}
.App {
  text-align: center;
}

body,html{
  margin: 0px;
  background-color: #f2f2f2;
}
h1,h2{
  font-family: 'Michroma', sans-serif;
}
p{
  font-family: 'PT Sans', sans-serif;
}
h5{
  font-family: 'Michroma', sans-serif;
}
.main-title{
  font-size: 40px;
  color: #141c3a;
  margin-bottom: 20px;
}
.text-dark{
  font-size: 20px;
  color: #141c3a;
  margin-top: 20px;
}
.text-dark-24{
  font-size: 24px;
  color: #141c3a;
  margin-bottom: 80px;
}
.text-purple{
  color: #6E07F3;
  font-family: 'PT Sans', sans-serif;
  font-size: 18px;
  font-weight: 400;
}
.text-light{
  font-size: 20px;
  color: #fff;
  line-height: 30px;
  max-width: 700px;
  display: block;
  margin: 0px auto;
}
.about{
  background-color: #6E07F3;
  padding: 60px 0px;
  height: 450px;
}
.about h1{
  color: #fff;
  margin-bottom: 20px;
}

.tools ul{
  display: flex;
  padding: 0px;
  justify-content: space-evenly;
}
.tools ul li:first-child{
  border-right: solid 1px #E6ECF8;
}
.hero{
  margin-top: 200px;
  margin-bottom: -1px;
}
.hero img{
  display: block;
  margin-left: auto;
  margin-right: auto;
}
.devices{
  width: 45%;
  margin-top: 80px;
}
.tools{
  background-color: #f2f2f2;
  box-shadow: 0 5px 5px 0 rgba(233, 240, 243, 0.5), 0 0 0 1px #E6ECF8;
  border-radius: 12px;
  width: 80%;
  display: block;
  margin: 0px auto;
  position: relative;
  bottom: 250px;
  border: solid 1px #E6ECF8;
}
.tools li{
  padding: 35px 60px;
  width: 50%;
}
.tools p{
  font-size: 18px;
  line-height: 30px;
  max-width: 350px;
  display: block;
  margin: 20px auto;
}
.grid-container{
  display: grid;
  padding: 0px;
  grid-template-columns: repeat(3, 1fr);
  gap: 20px;
  max-width: 1300px;
  margin: 20px auto;
}
.grid-container li{
  width: fit-content;
}
.grid-container img{
  border-radius: 12px;
  box-shadow: 0 5px 5px 0 rgba(233, 240, 243, 0.5), 0 0 0 1px #E6ECF8;
  border: solid 1px #E6ECF8;
}
.work{
  margin-top: -150px;
}
.work h1{
  color: #141c3a;
}
.work a{
  color: #6E07F3;
  text-decoration: none;
}
.box-dark{
  background-color: #141c3a;
  box-shadow: 0 5px 5px 0 rgba(0, 0, 0, 0.2), 0 0 0 1px #141c3a;
  color: #f2f2f2;
  width: 90%;
  margin: auto;
  border-radius: 12px;
  padding: 70px 20px;
  z-index: 1;
  position: relative;
  margin-top: 100px;
}
.box-dark ul{
  display: flex;
  align-items: center;
  justify-content: space-evenly; 
  padding: 0px;
}
.box-dark ul h2{
  font-size: 24px;
}
.box-dark ul p {
  max-width: 270px;
  font-size: 18px;
  line-height: 27px;
}
.box-dark ul a{
  color: #f2f2f2;
  text-decoration: none;
  font-size: 18px;
  border: solid 2px #5BE9B9;
  border-radius: 30px;
  padding: 15px 30px;
  transition: all .5s;
}
.box-dark ul a:hover{
  color: #141c3a;
  background-color: #5BE9B9;
  
}
.box-purple{
  background-color: #6E07F3;
  height: 300px;
  margin-top: -95px;
  padding-top: 150px;
}
.box-purple p{
  color: #f2f2f2;
  font-size: 24px;
  line-height: 30px;
  max-width: 300px;
  margin: auto;
  opacity: 0.8;
}
.links{
  margin: 60px 0px;
}
#link{
  color: #f2f2f2;
  border: solid 2px #f2f2f29c;
  border-radius: 100%;
  padding: 10px;
  margin: 0px 10px;
}
#link:hover{
  background-color: #f2f2f2;
  color: #6E07F3;
  transition: all .5s;
}
@media only screen and (max-width: 600px){
  .main-title{
    font-size: 24px;
    max-width: 90%;
    margin-left: auto;
    margin-right: auto;
  }
  .text-dark-24{
    font-size: 18px;
    max-width: 90%;
    margin-left: auto;
    margin-right: auto;
  }
  .devices{
    width: 90%;
  }
  .about h1{
    font-size: 24px;
  }
  .text-light{
    font-size: 16px;
    max-width: 90%;
    line-height: 24px;
  }
  .tools{
    width: 90%;
    bottom: 200px;
  }
  .tools ul{
    flex-direction: column;
    padding: 15px 20px;
  }
  .tools li{
    width: 100%;
    padding: 15px 0px;
  }
  .tools p{
    font-size: 16px;
    line-height: 24px;
    margin: 15px 0px;
  }
  .tools ul li:first-child{
    border-right: none;
    border-bottom: solid 1px #E6ECF8;
  }
  .work{
    width: 90%;
    margin-left: auto;
    margin-right: auto;
    margin-top: -120px;
  }
  .work h1{
    font-size: 24px;
  }
  .text-dark{
    font-size: 16px;
  }
  .grid-container{
    grid-template-columns: repeat(1, 1fr);
    padding: 0px;
  }
  .grid-container img{
    max-width: 90%;
  }
  .box-dark{
    padding: 40px 0px;
  }
  .box-dark ul{
    display: block;
  }
  .box-dark ul h2{
    font-size: 18px;
  }
  .box-dark ul p{
    font-size: 16px;
    margin: 15px auto;
  }
  .box-dark ul a{
    font-size: 16px;
    padding: 10px 25px;
    display: block;
    width: fit-content;
    margin: auto;
  }
  .box-purple p{
    font-size: 20px;
  }
  .links{
    margin: 40px 0px;
  }
  .box-purple{
    height: 260px;
  }
}
@media only screen and (min-device-width: 768px) and (max-device-width: 1024px){
  .grid-container{
    grid-template-columns: repeat(2, 1fr);
    padding: 0px;
  }
  .grid-container img{
    max-width: 90%;
  }
}